@import url(https://fonts.googleapis.com/css2?family=Besley:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background: url(/shopHM/static/media/the-fruit-3134631_1920.018c10ae.jpg);
    background-size: 100% auto;
    margin: 0;
    padding: 0;
    /* overflow: hidden; */
    font-family: 'Besley', serif;
}
.App{
    width: 100%;
    height: 100%;
}
header{
    background-color: rgba(236, 146, 29, 0.5);
    width: 100%;
    height: 70px;    
}
.row{
    display: flex;
    justify-content: center;
    align-items: center;
}
/* center */
header a{
    color: black;
    font-size: 20px;
    margin: 10px;
    border: 1px solid rgb(238, 136, 19);
    border-radius: 50px; 
}
header a:hover{
    box-shadow: 0 0 2px 0 #ffffff inset, 0 0 2px 2px #f84316;
}
.button_r_l:hover{
    box-shadow: 0 0 2px 0 #ffffff inset, 0 0 2px 2px #f84316;
}
.register_Login{
    -webkit-animation: append 1s linear 1;
            animation: append 1s linear 1;
    flex-direction: column; 
    background-color: rgba(238, 145, 24, 0.7);
    margin: 15% 35% 0% 35%;
    height: 230px;
}
/* 1.up  2.right 3.down 4.left*/
.input_r_l{
    margin: 7px;
    width: 35%;
    height: 25px;
    border: 1px solid orange;
    border-radius: 10px;
}
.back, .button_r_l{
    width: 70px;
    height: 30px;
    color: white;
    border: 1px solid rgb(255, 94, 0);
    background-color: rgb(255, 94, 0);
    border-radius: 10px;
    font-family: 'Besley', serif;
    cursor: pointer;
}
.card-wrapper{
    width: 100%;
    height: auto;
    flex-wrap: wrap;
    margin-top: 20px;
}
.card, .cart{
    width: 360px;
    height: 670px;
    background-color: rgba(236, 146, 29, 0.75);
    margin: 20px;
    font-size: 17px;
    padding: 4px;
}
.info_person{
    display: flex;
    justify-content: end;
    margin: -40px 5px;  
}
.btn_person{
    width: 70px;
    height: 30px;
    color: white;
    margin-left: 20px;
    border: 1px solid rgb(255, 94, 0);
    background-color: rgb(255, 94, 0);
    border-radius: 10px;
    font-family: 'Besley', serif;
    cursor: pointer;   
}
.icon_i{
    margin-left: 50%;
    border: 3px solid orange;
    border-radius: 50%;
    padding: 3px;
    color: rgb(255, 115, 0);
    background-color: white;
    cursor: pointer;
}
.sale{
    background-color: rgb(167, 0, 0);
    border-radius: 20px;
    width: 40px;
    height: 40px;
    padding: 2px;
}
.loader{
    background-color: rgba(255, 115, 0, 0.5);
    border: 3px solid orange;
    border-radius: 10px;
}
.pagination{
    background-color: rgba(255, 115, 0, 0.76);
    cursor: pointer;
    position: sticky;
    bottom: 0;
}
.pagination .active{
    background-color: rgb(248, 69, 15);
    width: 15px;
    border: 3px solid orange;
    border-radius: 10px;
}

.product_info{
    background-color: rgba(255, 115, 0, 0.7);
    margin: 10px;
}
.product_text_info{
    font-size: 20px;
    margin: 15px;
}
.color {
    width: 20px;
    height: 20px;
    border-radius: 50%;
  }
  
  @media screen and (max-width: 1200px){
      .register_Login{
        margin: 25% 15% 0% 15%;
      }
  }


  

@-webkit-keyframes append {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
  
    100% {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes append {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
  
    100% {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  /* трансформ разновидность */
